<template><section><html><head></head><body><h1>PrimitiveCollection</h1>
<p>The <code v-pre="">vc-collection-primitive</code> component is used to load a collection of primitives.This is most often used with Scene#primitives, but PrimitiveCollection is also a primitive itself so collections can be added to collections forming a hierarchy.</p>
<h2>Example</h2>
<h3>Load PrimitiveCollection with <code v-pre="">vc-collection-primitive</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive @click="clicked" :show="show">
          <vc-collection-primitive-billboard :billboards="billboards1"></vc-collection-primitive-billboard>
          <vc-collection-primitive>
            <vc-collection-primitive-billboard :billboards="billboards2"></vc-collection-primitive-billboard>
          </vc-collection-primitive>
        </vc-collection-primitive>
        <vc-collection-primitive :polygons="polygons">
          <vc-primitive-model @click="clicked" :url="url" :model-matrix="modelMatrix" :scale="10000" :minimum-pixel-size="128" :maximum-scale="200000">
          </vc-primitive-model>
          <vc-polygon :positions="positions"></vc-polygon>
        </vc-collection-primitive>
      </vc-viewer>
      <div class="demo-tool">
        <span>{{ show ? 'Hide' : 'Show' }}</span>
        <md-switch v-model="show"></md-switch>
      </div>
    </div>
  </template>
  
</doc-preview>
