<script>
    export default {
      data() {
        return {
          show: true,
          positions: [
            [105, 32],
            [106, 34],
            [107, 30]
          ],
          billboards1: [],
          billboards2: [],
          url: './statics/SampleData/models/CesiumAir/Cesium_Air.gltf',
          modelMatrix: {},
          polygons: [
            {
              positions: [
                [115, 37],
                [115, 32],
                [107, 33],
                [102, 31],
                [102, 35]
              ],
              appearance: {
                type: 'MaterialAppearance',
                options: {
                  material: 'green'
                }
              }
            },
            {
              positions: [
                { lng: 108.0, lat: 42.0 },
                { lng: 100.0, lat: 42.0 },
                { lng: 104.0, lat: 40.0 }
              ],
              appearance: {
                type: 'MaterialAppearance',
                options: {
                  material: 'red'
                }
              },
              depthFailAppearance: {
                type: 'MaterialAppearance',
                options: {
                  material: 'red'
                }
              }
            },
            {
              positions: [90.0, 41.0, 0.0, 85.0, 41.0, 500000.0, 80.0, 41.0, 0.0],
              appearance: {
                type: 'MaterialAppearance',
                options: {
                  material: 'blue'
                }
              }
            },
            {
              polygonHierarchy: {
                positions: [
                  [99, 30],
                  [85, 30],
                  [85, 40],
                  [99, 40]
                ],
                holes: [
                  {
                    positions: [
                      [97, 31],
                      [97, 39],
                      [87, 39],
                      [87, 31]
                    ],
                    holes: [
                      {
                        positions: [
                          [95, 33],
                          [89, 33],
                          [89, 37],
                          [95, 37]
                        ],
                        holes: [
                          {
                            positions: [
                              [93, 34],
                              [91, 34],
                              [91, 36],
                              [93, 36]
                            ]
                          }
                        ]
                      }
                    ]
                  }
                ]
              },
              appearance: {
                type: 'MaterialAppearance',
                options: {
                  material: 'yellow'
                }
              }
            }
          ]
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          const billboards1 = []
          const billboards2 = []
          for (var i = 0; i < 100; i++) {
            let billboard1 = {}
            billboard1.position = { lng: Math.random() * 40 + 85, lat: Math.random() * 30 + 21 }
            billboard1.image = 'https://zouyaoji.top/vue-cesium/favicon.png'
            billboard1.scale = 0.1
            billboards1.push(billboard1)

            let billboard2 = {}
            billboard2.position = { lng: Math.random() * 40 + 85, lat: Math.random() * 30 + 21 }
            billboard2.image = 'https://zouyaoji.top/vue-cesium/favicon.png'
            billboard2.scale = 0.15
            billboards2.push(billboard2)
          }
          this.billboards1 = billboards1
          this.billboards2 = billboards2
          this.modelMatrix = Cesium.Transforms.eastNorthUpToFixedFrame(Cesium.Cartesian3.fromDegrees(105, 38, 10000))
        },
        clicked(e) {
          console.log(e)
        }
      }
    }
  </script>